import React from 'react';
import { Box, Typography, Card, CardContent, Grid, Divider, LinearProgress } from '@mui/material';

// Use the PUBLIC_URL for GitHub Pages compatibility
const baseUrl = process.env.PUBLIC_URL || '';

// Define image paths with the PUBLIC_URL prefix
const featureImportance = `${baseUrl}/output/plots/feature_importance.png`;
const predictionVsActual = `${baseUrl}/output/plots/prediction_vs_actual.png`;

interface ModelCardProps {
  name: string;
  description: string;
  strengths: string[];
  weaknesses: string[];
  rmse: number;
  r2: number;
}

const ModelCard: React.FC<ModelCardProps> = ({ 
  name, 
  description, 
  strengths, 
  weaknesses, 
  rmse,
  r2
}) => {
  // Normalize RMSE for display (lower is better)
  // Assuming max RMSE is 3.0 for visualization purposes
  const rmseNormalized = 100 - ((rmse / 3.0) * 100);
  
  // R2 is already between 0-1, just multiply by 100
  const r2Normalized = r2 * 100;
  
  return (
    <Card sx={{ height: '100%' }}>
      <CardContent>
        <Typography variant="h5" gutterBottom>
          {name}
        </Typography>
        <Typography variant="body2" paragraph>
          {description}
        </Typography>
        
        <Box sx={{ mb: 2 }}>
          <Typography variant="subtitle2" gutterBottom>
            Model Performance
          </Typography>
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
            <Typography variant="body2" sx={{ minWidth: 180 }}>
              RMSE (Root Mean Square Error):
            </Typography>
            <Box sx={{ width: '100%', mr: 1 }}>
              <LinearProgress 
                variant="determinate" 
                value={rmseNormalized} 
                color="primary"
                sx={{ height: 8, borderRadius: 4 }}
              />
            </Box>
            <Typography variant="body2" sx={{ minWidth: 50 }}>
              {rmse.toFixed(2)}
            </Typography>
          </Box>
          
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography variant="body2" sx={{ minWidth: 180 }}>
              R² (Coefficient of Determination):
            </Typography>
            <Box sx={{ width: '100%', mr: 1 }}>
              <LinearProgress 
                variant="determinate" 
                value={r2Normalized} 
                color="success"
                sx={{ height: 8, borderRadius: 4 }}
              />
            </Box>
            <Typography variant="body2" sx={{ minWidth: 50 }}>
              {r2.toFixed(2)}
            </Typography>
          </Box>
        </Box>
        
        <Divider sx={{ my: 2 }} />
        
        <Typography variant="subtitle2" gutterBottom>
          Strengths
        </Typography>
        <ul>
          {strengths.map((strength, index) => (
            <Typography component="li" variant="body2" key={`strength-${index}`}>
              {strength}
            </Typography>
          ))}
        </ul>
        
        <Typography variant="subtitle2" gutterBottom>
          Limitations
        </Typography>
        <ul>
          {weaknesses.map((weakness, index) => (
            <Typography component="li" variant="body2" key={`weakness-${index}`}>
              {weakness}
            </Typography>
          ))}
        </ul>
      </CardContent>
    </Card>
  );
};

